"use client";

import { useState } from "react";
import LeftSideLayout from "@/components/common/Leftsidelayout";
import Navbar from "@/components/common/Navbar";
import { motion } from "framer-motion";

export default function AvailabilityPage() {
  const [timeZone, setTimeZone] = useState("Eastern Time (ET)");
  const [respondTime, setRespondTime] = useState("Within 3hrs");
  const [selectedDays, setSelectedDays] = useState<string[]>([]);
  const [urgent, setUrgent] = useState(true);

  const handleDayToggle = (day: string) => {
    setSelectedDays((prev) =>
      prev.includes(day) ? prev.filter((d) => d !== day) : [...prev, day]
    );
  };

  const quickSelect = (type: "weekdays" | "weekends" | "everyday") => {
    if (type === "weekdays")
      setSelectedDays(["Monday", "Tuesday", "Wednesday", "Thursday", "Friday"]);
    else if (type === "weekends") setSelectedDays(["Saturday", "Sunday"]);
    else
      setSelectedDays([
        "Monday",
        "Tuesday",
        "Wednesday",
        "Thursday",
        "Friday",
        "Saturday",
        "Sunday",
      ]);
  };

  return (
    <div className="flex h-screen bg-white">
      {/* ✅ Left Section (Reuse) */}
      <LeftSideLayout
        title="Earn money your way"
        subtitle="See how much you can make tasking on Workly"
        rating="Trusted by 5k+ Pros"
      />

      {/* ✅ Right Section */}
      <div className="flex flex-col flex-1 overflow-y-auto">
        <Navbar />

        <motion.div
          initial={{ opacity: 0, x: 30 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{ duration: 0.6, ease: "easeOut" }}
          className="flex flex-col items-center justify-center flex-1 px-8"
        >
          <div className="w-full max-w-lg">
            {/* Step Header */}
            <div className="mb-6">
              <h2 className="text-lg font-semibold text-gray-800">
                Step 4 : Set your Available Times
              </h2>
              <div className="mt-2 h-2 w-full bg-gray-200 rounded-full">
                <div className="h-2 bg-blue-600 rounded-full w-2/3"></div>
              </div>
              <p className="text-sm text-gray-500 mt-1">4 / 6</p>
            </div>

            {/* Form Box */}
            <div className="bg-white rounded-2xl shadow-md p-6 border border-gray-100">
              {/* Time Zone + Respond Time */}
              <div className="grid grid-cols-2 gap-4 mb-5">
                <div>
                  <label className="text-sm font-medium text-gray-700">
                    Time Zone*
                  </label>
                  <select
                    value={timeZone}
                    onChange={(e) => setTimeZone(e.target.value)}
                    className="w-full mt-1 border border-gray-300 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
                  >
                    <option>Eastern Time (ET)</option>
                    <option>Central Time (CT)</option>
                    <option>Mountain Time (MT)</option>
                    <option>Pacific Time (PT)</option>
                  </select>
                </div>
                <div>
                  <label className="text-sm font-medium text-gray-700">
                    Responds Time*
                  </label>
                  <select
                    value={respondTime}
                    onChange={(e) => setRespondTime(e.target.value)}
                    className="w-full mt-1 border border-gray-300 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
                  >
                    <option>Within 3hrs</option>
                    <option>Within 6hrs</option>
                    <option>Within 12hrs</option>
                    <option>Within 24hrs</option>
                  </select>
                </div>
              </div>

              {/* Quick Presets */}
              <div className="flex items-center gap-2 mb-3">
                <span className="text-sm font-medium text-gray-700">
                  Quick Presets:
                </span>
                <button
                  onClick={() => quickSelect("weekdays")}
                  className="text-xs px-3 py-1 bg-blue-100 rounded-full text-blue-700 hover:bg-blue-200 transition"
                >
                  Weekdays
                </button>
                <button
                  onClick={() => quickSelect("weekends")}
                  className="text-xs px-3 py-1 bg-blue-100 rounded-full text-blue-700 hover:bg-blue-200 transition"
                >
                  Weekends
                </button>
                <button
                  onClick={() => quickSelect("everyday")}
                  className="text-xs px-3 py-1 bg-blue-100 rounded-full text-blue-700 hover:bg-blue-200 transition"
                >
                  Everyday
                </button>
                <button
                  onClick={() => setSelectedDays([])}
                  className="text-xs text-gray-400 hover:text-gray-600 ml-auto"
                >
                  Clear
                </button>
              </div>

              {/* Day List */}
              <div className="grid grid-cols-2 gap-2 mb-5">
                {[
                  "Monday",
                  "Tuesday",
                  "Wednesday",
                  "Thursday",
                  "Friday",
                  "Saturday",
                  "Sunday",
                ].map((day) => (
                  <label
                    key={day}
                    className={`border rounded-lg px-3 py-2 flex items-center justify-between cursor-pointer transition ${
                      selectedDays.includes(day)
                        ? "bg-blue-50 border-blue-500"
                        : "hover:bg-gray-50"
                    }`}
                  >
                    <span className="text-sm text-gray-700">{day}</span>
                    <input
                      type="checkbox"
                      checked={selectedDays.includes(day)}
                      onChange={() => handleDayToggle(day)}
                      className="w-4 h-4 accent-blue-600"
                    />
                  </label>
                ))}
              </div>

              {/* Urgent Request Toggle */}
              <div className="flex items-center justify-between border-t pt-3">
                <div>
                  <h4 className="text-sm font-medium text-gray-700">
                    Accept Urgent Requests
                  </h4>
                  <p className="text-xs text-gray-500">
                    Get notified for urgent jobs even outside your working hours
                  </p>
                </div>

                {/* Custom toggle switch */}
                <div
                  className={`w-12 h-6 flex items-center rounded-full p-1 cursor-pointer transition ${
                    urgent ? "bg-blue-600" : "bg-gray-300"
                  }`}
                  onClick={() => setUrgent(!urgent)}
                >
                  <div
                    className={`bg-white w-5 h-5 rounded-full shadow-md transform transition ${
                      urgent ? "translate-x-6" : "translate-x-0"
                    }`}
                  ></div>
                </div>
              </div>
            </div>

            {/* Buttons */}
            <div className="flex justify-between mt-6">
              <button className="w-32 border border-gray-300 text-gray-700 rounded-lg py-2 hover:bg-gray-100 transition">
                Back
              </button>
              <button className="w-32 bg-blue-600 text-white rounded-lg py-2 hover:bg-blue-700 transition">
                Next
              </button>
            </div>
          </div>
        </motion.div>
      </div>
    </div>
  );
}
